/* (A) FIXED WRAPPER */
.hwrap {
    position: absolute;
    width: 85%;
    height: 38px;
    overflow: hidden;
    left: 13em;
    top: 1em;
    border: 1px solid /*#8D8D8D*/ rgba(0, 0, 0, 0.5);
    border-radius: 2.5px;
    background: rgba(255, 255, 255, 0);
    float: right;
    box-shadow: inset 12px 0px 12px 0px #fffbd6 , inset -12px 0px 12px 0px #FFFDEB , 6px 0px 30px 0px #FFFFFF;
  }

  /* (A) FIXED WRAPPER 
.nino {
  position: absolute;
  width: 80px;
  height: 80px;
  overflow: hidden;
  left: 87.75em;
  top: 1em;
  border: 0.5px solid #0e0adf;
  border-radius: 2.5px;
  float: right;
  box-shadow: inset 6px 0px 6px 0px rgba(14, 38, 248, 0.13), inset -6px 0px 6px 0px rgba(14, 38, 248, 0.13), 3px 0px 15px 0px rgba(2, 19, 255, 0.685);
}
*/

  .hwrap:hover {
    border: 1.5px solid rgb(255, 255, 255);
    box-shadow: inset 6px 0px 6px 0px rgba(255, 255, 255, 0.13), inset -6px 0px 6px 0px rgba(29, 172, 255, 0.281), 6px 0px 15px 0px rgba(161, 255, 255, 0.685);
}
   
  /* (B) MOVING TICKER WRAPPER */
  .hmove { display: flex; }
  
  /* (C) ITEMS - INTO A LONG HORIZONTAL ROW */
  .hitem {
    flex-shrink: 0;
    width: 100%;
    box-sizing: border-box;
    padding: 6.5px;
    text-align: center;
    font-size: 1.1rem;
  }
   
  /* (D) ANIMATION - MOVE ITEMS FROM RIGHT TO LEFT */
  /* 4 ITEMS -400%, CHANGE THIS IF YOU ADD/REMOVE ITEMS */
  @keyframes tickerh {
    0% { transform: translate3d(100%, 0, 0); }
    100% { transform: translate3d(-400%, 0, 0); }
  }
  .hmove { animation: tickerh linear 30s infinite; }

  .hmove:hover { 
      animation-play-state: paused;
      color: rgba(230, 240, 255, 0.99);
      text-shadow: 0px 0px 2px rgb(255, 132, 132), 0px 0px 4px rgba(255, 156, 185, 0.99);
      border-color:  rgba(255, 3, 3, 0.4);
      cursor: pointer;
     }

